import React, { useState, useCallback, useEffect, useRef } from 'react'
import { Debounce } from '../Debounce'
import Button from '../components/Button'
import ResultNum from '../components/ResultNum';
import LineDiv from '../components/LineDiv';
import styled from 'styled-components'
import NumLineWrap from '../components/NumLineWrap';
// 
const Div = styled.div`
    border-top : 1px solid rgba(134,134,134,0.19);
    background: rgba(134,134,134,0.07);
    animation: ${props => props.theme.fadeUp} .5s linear alternate;
    margin-bottom : 15px;
    margin-top : 15px;
`
const SmallSpan = styled.span`
    color : gray;
    font-size : 12px;
`
type props = {
    list: number[][]
    setDraw: Function
    setCorrect: Function
    correct: boolean[]
    setbonusCorrect: Function
    bonusCorrect: boolean[]
    setTrigger: Function
    trigger: boolean
    mode: boolean
}

const Draw = ({ setTrigger, list, setCorrect, correct, setbonusCorrect, bonusCorrect, mode }: props) => {

    //useSelector state 
    // var list = useSelector((state: StoreState) => state.Reducer.list);
    const [result, setResult] = useState<number[]>([]);
    //   boolean .  style component  props state . 
    // const [correct,setCorrect] = useState<boolean[]>([])
    
    // 
    const interval = useRef<any>();



    useEffect(() => {
        //    
        if (mode) {
            random([])
            stop()
        }
    }, [])

    //  . 50ms text  .    .
    const random = (arr: Array<number>) => {
        document.getElementById('startBtn')?.setAttribute("disabled", "true");
        var num = document.getElementById("num")
        interval.current = setInterval(() => {
            if (!num) clearInterval(interval.current)
            while (num) {
                let number = Math.floor((Math.random() * 45) + 1)
                if (!arr.includes(number)) {
                    num.innerText = number.toString()
                    break;
                }
            }
        }, 50);
    }


    //state      interval    usecallback   .
    const stop = Debounce(() => {
        // 
        clearInterval(interval.current)
        //  .
        let text:any = document.getElementById('num')?.innerText
        let num = parseInt(text)
        if (num === 0) return

        //         correct 
        for (var i = 0; i < list.length; i++) {
            if (list[i].includes(num)) {
                if (result.length !== 6) {
                    correct[num] = true;
                    setCorrect([...correct])
                    break
                }
                else {
                    bonusCorrect[num] = true;
                    setbonusCorrect([...bonusCorrect])
                    break
                }
            }
        }
        // list.forEach(i => {
        //     if (i.includes(num)) {
        //         if (result.length !== 6) {
        //             correct[num] = true;
        //             setCorrect([...correct])
        //             console.log(2)
        //             return
        //         }
        //         else {
        //             bonusCorrect[num] = true;
        //             setbonusCorrect([...bonusCorrect])
        //             console.log(3)
        //             return
        //         }
        //     }
        //     console.log(1)
        // })

        //  
        setResult([...result, num])


        //   600ms     . 
        //            
        if (result.length < 6)
            setTimeout(() => {
                random([...result, num])
                if (mode)
                    document.getElementById('stopBtn')?.click()
            }, 600);

        //    true  Rank    .
        else {
            document.getElementById("loadingBg")!.style.display = "none";
            document.getElementById('stopBtn')!.setAttribute("disabled", "true");
            setTrigger(true)
        }
    }, 700)


    //.      .
    const reset = () => {
        clearInterval(interval.current)
        document.getElementById('num')!.innerText = "0"
        setCorrect([])
        setbonusCorrect([])
        setResult([])
        setTimeout(() => {
            setTrigger(false)
        }, 200);

        document.getElementById('stopBtn')!.removeAttribute("disabled")
        document.getElementById('startBtn')!.removeAttribute("disabled")
    }

    return (
        <Div id="drawSec">
            <h1 id="num" style={{ fontSize: "30px" }}>0</h1>
            <div style={{display : mode ? "none" : "block"}}>
                <Button fontSize="1.1em" id="startBtn" content="" click={() => random([])}></Button>
                <Button fontSize="1.1em" id="stopBtn" click={stop} content=""></Button>
                <Button fontSize="1.1em" id="resetBtn" color="rgb(86, 115, 235)" bg="rgb(224, 230, 251)" click={reset} content=""></Button>
            </div>
            <LineDiv fontSize={15} content=" "></LineDiv>
            <NumLineWrap idx={0} content={result.length === 0 ? <SmallSpan id="beforeDrawNotice">    </SmallSpan> : result.map((x, idx) => {
                if (idx === 6)
                    return (
                        <span key={idx} id="bonusSpan">+  <ResultNum key={idx} bonusCorrect={bonusCorrect[x]} correct={correct[x]} bonus={true} num={x}></ResultNum></span>
                    )
                else
                    return (
                        <ResultNum key={idx} bonusCorrect={bonusCorrect[x]} correct={correct[x]} num={x}></ResultNum>
                    )
            })}></NumLineWrap>
        </Div>
    )
}

export default Draw
